	module.exports = {"data":{"site":{"siteMetadata":{"title":"Solar Sailer","description":"I am a developer, designer and product manager from France. Co-founder of Pixelnest Studio.","author":"Matthieu Oger","siteUrl":"https://solarsailer.net/"}},"markdownRemark":{"html":"<p><a href=\"http://pornel.net/beat-gif-manifesto\">HTML video still can't beat GIF</a>:</p>\n<blockquote>\n<p>Animated GIF, despite having awful compression and terrible quality, is still more useful on the web than any other video format.</p>\n<p>In 2013 people still routinely convert YouTube clips to GIF (producing postage-stamp-sized dithered-to-death desaturated blobs that use more bandwidth than a full HD video), because there's still no better way to share a short movie clip on the web.</p>\n</blockquote>\n<p>Gifs are an abomination: they are heavy and the quality is, most of the time, terrible. However, their convenience is too good that people use them nonetheless.</p>\n<p>The electrical cost of gifs must be gigantic.</p>\n<hr>\n<h2>Update <span class=\"more\">08 June 2018</span></h2>\n<p>There's <a href=\"https://calendar.perfplanet.com/2017/animated-gif-without-the-gif/\">hope</a>. In a near future, and in other browsers than Safari, we will be able to use a video as an image to <em>simulate a gif</em> (ie., silence and autoplay). With perks of a video instead of an awful low-quality and heavy gif.</p>","frontmatter":{"date":"2013-08-29","readableDate":"29 August 2013","title":"The gif “video format”","showPageTitle":null,"excerptLine":null,"excerptQuote":{"name":"Kornel","quote":"Animated GIF, despite having awful compression and terrible quality, is still more useful on the web than any other video format."}},"fields":{"path":"/2013/08/the-gif-video-format/"}}},"pathContext":{"id":"/Users/ashen/Code/solarsailer.net/content/posts/2013-08-29-the-gif-video-format.md absPath of file >>> MarkdownRemark"}}

